<template>
  <div class="forgot">
    <div class="topBgcolor"></div>

    <template>
      <div class="login">
        <!--登录页面 -->
        <div class="login-box">
          <!-- 顶部导航 -->
          <van-nav-bar>
            <template #left>
              <div class="left-text">
                <div class="logo">
                  <img
                    class="auto-img"
                    src="../assets/images/home_active.png"
                    alt=""
                  >
                </div>
                <div class="logo-title">Luckin Coffce</div>
              </div>
            </template>
            <template #right>
              <div class="right-text">
                <div @click="goLogin()">返回登录</div>
              </div>
            </template>
          </van-nav-bar>

          <!--  -->
          <div class="login-form">
            <div class="title">忘记密码！</div>
            <div class="en-title">Forget the password!</div>

            <!-- 表单 -->
            <div class="login-form-box">
              <van-form>
                <van-field
                  class="border-bottom"
                  label="手机号："
                  placeholder="手机号"
                  v-model="userInfo.phone"
                />
                <van-field
                  class="border-bottom"
                  label="邮 箱："
                  placeholder="邮箱"
                  v-model="userInfo.email"
                />
                <van-field
                  class="border-bottom"
                  label="验证码："
                  placeholder="请输入邮箱验证码"
                  center
                  clearable
                  v-model="userInfo.validCode"
                >
                  <template #button>
                    <van-button
                      size="small"
                      color="#7c90cf"
                      :disabled="isSend"
                      @click="sendValidCode"
                    >{{text}}</van-button>
                  </template>
                </van-field>
                <div
                  class="clearfix forgot-box"
                  @click="goLogin"
                >
                  <span class="fr">已有帐号，立即登录</span>
                </div>
                <div class="login-btn">
                  <van-button
                    block
                    type="info"
                    native-type="submit"
                    @click="commit"
                  >下&nbsp;&nbsp;一&nbsp;&nbsp;步</van-button>
                </div>
              </van-form>
            </div>
          </div>
        </div>
      </div>
    </template>

  </div>
</template>

<script>
import "../assets/less/forgot.css";
import { validForm } from "../assets/js/validForm";
import { utils } from "../assets/js/utils";

export default {
  data() {
    return {
      userInfo: {
        phone: "",
        email: "",
        validCode: "",
      },
      text: "发送验证码",
      isSend: false,
    };
  },

  created() {},

  methods: {
    goLogin() {
      this.$router.push({ name: "Login" });
    },

    // 发送验证码
    sendValidCode() {
      // 发起获取邮箱验证码请求
      // 开启加载提示
      this.$toast.loading({
        message: "加载中...",
        // 防止穿透
        forbidClick: true,
        // 图标
        loadingType: "spinner",
        // 显示时间，如果时间位0，则不会自动关闭
        duration: 0,
      });

      //   序列化参数
      let data = utils.queryString({
        email: this.userInfo.email,
        appkey: this.appkey,
      });

      this.axios({
        method: "POST",
        //   请求参数
        url: "/emailValidCode",
        data,
      })
        .then((result) => {
          //   关闭加载提示;
          this.$toast.clear();
          

          this.$notify({
            type: "warning",
            message: result.data.msg,
          });

          if (result.data.code == "J001") {
            let time = 60;
            this.text = time + "s后重新发送";
            this.isSend = true;

            let timer = setInterval(() => {
              if (time == 0) {
                clearInterval(timer);
                this.isSend = false;
                this.text = "发送验证码";
              } else {
                time--;
                this.text = time + "s后重新发送";
              }
            }, 1000);
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 验证邮箱验证码
    emailCode() {
      // 发起忘记密码请求
      this.$toast.loading({
        message: "加载中...",
        // 防止穿透
        forbidClick: true,
        // 图标
        loadingType: "spinner",
        // 显示时间，如果时间位0，则不会自动关闭
        duration: 0,
      });

      //   序列化参数
      let data = utils.queryString({
        validCode: this.userInfo.validCode,
        appkey: this.appkey,
      });

      this.axios({
        method: "POST",
        //   请求参数
        url: "/checkValidCode",
        data,
      })
        .then((result) => {
          //   关闭加载提示;
          this.$toast.clear();
          
          let phone = this.userInfo.phone;

          if (result.data.code == "K001") {
            this.$router.push({ name: "Nextforgot", query: { phone } });
          } else {
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 下一步
    commit() {
      //   验证修改密码表单
      let o = {
        phone: {
          value: this.userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "请输入11为手机号码",
        },
        email: {
          value: this.userInfo.email,
          reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
          errorMsg: "邮箱格式不正确",
        },
        validCode: {
          value: this.userInfo.validCode,
          reg: /^\d{6}$/,
          errorMsg: "请输入六位数字验证码",
        },
      };

      if (!validForm.valid(o)) {
        //   如果验证不通过
        return;
      }

      // 发起修改密码请求
      this.$toast.loading({
        message: "加载中...",
        // 防止穿透
        forbidClick: true,
        // 图标
        loadingType: "spinner",
        // 显示时间，如果时间位0，则不会自动关闭
        duration: 0,
      });

      //   序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        phone: this.userInfo.phone,
      });

      this.axios({
        method: "POST",
        //   请求参数
        url: "/retrievePassword",
        data,
      })
        .then((result) => {
          //   关闭加载提示;
          this.$toast.clear();
          

          if (result.data.code == "L004") {
            this.$toast("手机号码未注册");
          }else{
            // 验证email验证码是否准确
            this.emailCode()
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });

      
    },
  },
};
</script>